/**
 * @explain
 * This storybook decorator is used to wrap the content of the story in a
 * container. This is useful to limit the width of the content, and to center
 * it.
 */
import { FC } from 'react';
import { styled } from 'styled-components';

import { tablet, laptop } from '#theme/devices';

const Wrapper = styled.div`
  max-width: ${({ theme }) => theme.dimension.contentWidth.mobile}px;
  margin: 0 auto;

  @media ${tablet} {
    max-width: ${({ theme }) => theme.dimension.contentWidth.tablet}px;
  }

  @media ${laptop} {
    max-width: ${({ theme }) => theme.dimension.contentWidth.laptop}px;
  }
`;

const ContentDecorator = (Story: FC) => (
  <Wrapper>
    <Story />
  </Wrapper>
);

export default ContentDecorator;
